<template>
    <div class="container">
    <div class="row">
      <div class="col-md-6">
        <div class="f-col">
          <label class="l-header" style="padding-top:25px;">current volume</label>
          <label class="main-stat">{{ this.currentVolume.toFixed(3) }}</label>
        </div>
        <div class="f-col">
          <label class="l-header" style="padding-top:25px;">volume available</label>
          <label class="main-stat">{{ this.remainingVolume.toFixed(3) }}</label>
        </div>
        <div class="f-col">
          <label class="l-header" style="padding-top:25px;">percentage full</label>
          <label class="main-stat percent">{{ this.remainingVolumePercentage.toFixed(2) }}%</label>
        </div>
        <div style="padding-top:25px;">
            <label>Adjust Amount</label>
            <div style="display:flex; justify-content:center;">
              <input id="new-amount" @input="filterNumbersOnly" class="add-entry form-control" type="text" maxlength="20" />
              <div>
                <button @click="performAdjustment('add')" type="button" class="btn btn-success px-3" style="margin-left:25px;" alt="Add"><beaker-plus-outline /></button>
                <button @click="performAdjustment('minus')" type="button" class="btn btn-danger px-3" style="margin-left:5px;" alt="Remove"><beaker-minus-outline /></button>
              </div>
            </div>
        </div>
        <div style="padding-top:25px;">
            <label>Recent Adjustments</label>
            <div class="adjustment-holder">
                <div class="adjustment-entry" v-for="adjustment in this.adjustments" :key="adjustment.id">
                    <div class="ae-row">
                        <div>{{ adjustment.adjustmentType == "+" ? "added" : "removed" }}</div>
                        <div>&nbsp;{{ adjustment.adjustmentAmount }}</div>
                        <div>&nbsp;on {{ new Date(adjustment.adjustmentDate).toLocaleString() }}</div>
                        <div>
                            <button @click="removeById(adjustment.id)" type="button" class="btn btn-danger btn-sm" style="margin-left:15px;"><beaker-minus-outline /></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
      <div class="col-md-6">
        <div>
            <img src="@/assets/silo-top.png" alt="silo"> 
        </div>
        <div style="position:relative; display:flex; justify-content:center;">   
            <div id="divcover" class="cover" :style="{ height: this.volumeInPixels + 'px' }"></div>
            <img src="@/assets/silo-middle.png" alt="silo">            
        </div>
        <div>
            <img src="@/assets/silo-bottom.png" alt="silo">
        </div>
      </div>
    </div>
    <loading v-model:active="loading"
            :can-cancel="false"
            :is-full-page="fullPage"/>
  </div>
</template>

<script>
import BeakerPlusOutline from 'vue-material-design-icons/BeakerPlusOutline.vue';
import BeakerMinusOutline from 'vue-material-design-icons/BeakerMinusOutline.vue';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'vue-material-design-icons/styles.css';
import axios from 'axios';

export default {
  name: 'App',
  components: {
    BeakerPlusOutline, BeakerMinusOutline, Loading
  },
  computed: {
    remainingVolume: function() {
      return this.maxVolume - this.currentVolume;
    },
    remainingVolumePercentage: function() {
      return (100 * this.currentVolume) / this.maxVolume;
    },
    volumeInPixels: function() {
        var pixels = ((this.remainingVolumePercentage / 100) * 260).toFixed(0);

        return String(pixels);
    }
  },
  data() {
    return {
      apiUrl: 'https://api.radian.com.au/api/radian/silo/',
      maxVolume : 200.000,
      currentVolume : 0.000,
      adjustments : [],
      loading: false,
    }
  },
  methods: {
    removeById: async function(id) {
        if(confirm('you sure you want to delete all history of this entry?')) {
            this.loading = true;
            this.currentVolume = 0.000;
            let vue = this;

            const config = {
                method: 'get',
                url: (this.apiUrl + 'removesiloadjustmentbyid?id=' + id)
            }

            try {
                await axios(config).then(() => {
                    vue.getAllAdjustments().then(() => {        
                        alert('removed yo');
                        vue.loading = false;
                    });
                });
            }
            catch(ex){
                console.log(ex);
                vue.loading = false;
            }
        }
    },
    filterNumbersOnly: function (event) {
        event.target.value = event.target.value.match(/^[0-9]*\.?[0-9]*$/g, '');
    },
      getAllAdjustments: async function() {
          this.loading = true;
          this.currentVolume = 0.000;

        const config = {
            method: 'get',
            url: (this.apiUrl + 'getsiloadjustments')
        }

        await axios(config).then(res => {
            this.adjustments = res.data;

            for(const adjustment of this.adjustments) {
                switch(adjustment.adjustmentType) {
                    case '+':
                        this.currentVolume += adjustment.adjustmentAmount;                        
                        break;

                    default:
                        this.currentVolume -= adjustment.adjustmentAmount;                        
                        break;
                }                
            }
        });

        this.loading = false;
      },
    performAdjustment: async function (adjustmentType) {
        var amount = '', formattedAmount = '', config = {};
        let vue = this;

        this.loading = true;

        try{
        switch(adjustmentType) {
            case 'add':
                amount = document.getElementById('new-amount').value.trim();
                
                if((amount == null) || (amount == '')) {
                    this.loading = false;
                    alert('please enter an amount... dummy');
                    return;
                }

                formattedAmount = parseFloat(amount).toFixed(3);

                if(formattedAmount <= 0){
                    this.loading = false;
                    alert('amount must be > 0... weirdo');
                    return;
                }
                if(formattedAmount >= this.remainingVolume){
                    this.loading = false;
                    alert('why you enter more than what is available?... stoopid');
                    return;
                }
                if(formattedAmount + this.currentVolume >= this.maxVolume){
                    this.loading = false;
                    alert('why you enter more than what is available?... stoopid');
                    return;
                }

                config = {
                    method: 'get',
                    url: (this.apiUrl + 'addsiloadjustment?amount=' + formattedAmount)
                }

                await axios(config).then(() => {
                    vue.getAllAdjustments().then(() => {                    
                        this.loading = false;
                        document.getElementById('new-amount').value = '';
                        alert('awwwww snap, you did that');
                    });
                });                
                break;

            default:
                amount = document.getElementById('new-amount').value.trim();
                
                if((amount == null) || (amount == '')) {
                    this.loading = false;
                    alert('please enter an amount... dummy');
                    return;
                }

                formattedAmount = parseFloat(amount).toFixed(3);

                if(formattedAmount <= 0){
                    this.loading = false;
                    alert('amount must be > 0... weirdo');
                    return;
                }
                if(formattedAmount >= this.currentVolume){
                    this.loading = false;
                    alert('why you try remove more than what you have?... stoopid');
                    return;
                }

                config = {
                    method: 'get',
                    url: (this.apiUrl + 'removesiloadjustment?amount=' + formattedAmount)
                }

                await axios(config).then(() => {
                    vue.getAllAdjustments().then(() => {                    
                        this.loading = false;
                        document.getElementById('new-amount').value = '';
                        alert('awwwww snap, you did that');
                    });
                });   
                break;
        }
        }
        catch(ex){
            console.log(ex);
        }
        finally {
            this.loading = false;
        }
      }
  },
  mounted: function() {
      this.getAllAdjustments();
  }
}
</script>

<style scoped>
.adjustment-holder {
    max-height:125px;
    height:125px;
    overflow-y: scroll;
    overflow-x: hidden;
}
.cover {
    position:absolute;
    z-index: 100;
    bottom:15px;
    background-color:green;
    width:260px;
}

.adjustment-entry {
    font-style: italic;
    display:flex;
    justify-content: center;
    align-items: center;
}
    .adjustment-entry.green {
        color:green;
    }
    .adjustment-entry.red {
        color:red;
    }

    .adjustment-entry .ae-row{
        display:flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px gray dashed;
        padding: 3px 3px 3px 3px;
        width:350px;
    }

.f-col{
  display: flex;
  flex-flow: column;
}

.add-entry{
  max-width:250px;
}

.l-header{
  text-transform: uppercase;
  font-size: 16px;
}

.main-stat{
  font-weight: bold;
  font-size: 24px;
}
  .main-stat.percent {
    font-size:56px;
  }
</style>